@import url('https://fonts.googleapis.com/css2?family=Crimson+Text:wght@400;700&family=Inconsolata:wght@300;400&family=Lobster&family=Montserrat:wght@200;400&family=PT+Sans+Narrow&family=Poppins&family=Quicksand:wght@400;500&family=Raleway&family=Source+Sans+Pro:wght@300&family=Spirax&display=swap');

.aupairContentWrapper {
    padding: 100px;
    align-items: center;
    background-image: url('../img/wallpaper-background-several-transparent-circles_58702-7112.avif');
}

.globeImg {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 350px;
}

.aboutTitle {
    text-align: center;
    margin-bottom: 20px;
    color: #e9c46a;
    font-family: 'Courgette';
}

.aboutContent {
    margin-bottom: 30px;
    font-size: 18px;
    font-family: 'Inconsolata', sans-serif;
    line-height: 1.5;
}

.cardsWrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.countriesContentWrapper,
.aupairGainWrapper,
.hostFamiliesGainWrapper {
    border-radius: 10px;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
    border: 1px solid #e9c46a;
    max-width: 240px;
    max-height: fit-content;
    justify-content: center;
    padding: 30px;
    display: flex;
    flex-wrap: wrap;
    margin: 20px auto;
}

.aboutCountriesTitle {
    font-size: 20px;
    font-family: 'Quicksand', sans-serif;
    margin-bottom: 15px;
    text-align: center;
}

.countries {
    display: flex;
    align-items: center;
}

.countryName {
    font-size: 17px;
    margin-left: 18px;
    line-height: 1.5;
    font-style: italic;
}

.gainTitle {
    font-size: 23px;
    font-family: 'Quicksand', sans-serif;
    margin-bottom: 15px;
    text-align: center;
}

.gains {
    display: flex;
    margin: 14px auto;
}

.gainDescription {
    font-size: 17px;
    margin-left: 18px;
    line-height: 1.5;
    font-style: italic;
}

@media(max-width: 768px) {

    .globeImg {
        height: 280px;
    }

    .aboutTitle {
        font-size: 24px;
    }

    .aupairContentWrapper {
        padding: 30px;
    }

    .aboutContent {
        font-size: 15px;

    }

    .gainDescription {
        font-size: 15px;
        line-height: 1.3;
    }

    .gainTitle,
    .aboutCountriesTitle {
        font-size: 20px;
    }

    .countryName {
        font-size: 16px;
    }

    .countriesContentWrapper,
    .aupairGainWrapper,
    .hostFamiliesGainWrapper {
        max-width: 210px;
        background-color: #f2f2f2;
    }
}